.ecl-launch__container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
  width: 100%;
  &.logo {
    align-items: flex-end;
    > .fade-in {
      animation: fadeIn 1s ease-in-out;
    }
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
